//jquery
var $ = require("jquery");
$('p').css('color', 'red');
window.$ = $;

//vue
import Vue from "vue";

//Vue-router
import VueRouter from "vue-router";
Vue.use(VueRouter);

//Vuex
import Vuex from 'vuex';
Vue.use(Vuex);

//Muse-ui
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
Vue.use(MuseUI);

//mui
//require("./mui/css/mui.css");
//window.mui = require("./components/js/mui.js");

//weui
require("./components/css/weui.css");

//swiperjs
require("./components/css/swiper.css");
require("./components/js/swiper.js");

//
require("./components/css/commen.css");

//
//
var firstCp = require("./components/firstCp.vue");
//
var secondCp = require("./components/secondCp.vue");

//
var threeCp = require("./components/threeCp.vue");
//
var fourCp = require("./components/fourCp.vue");
var detailsCp = require("./components/details.vue");
var homeDetailCp=require("./components/homeDetail.vue");
var junshiDetailCp=require("./components/junshiDetail.vue");
var yuleDetailCp=require("./components/yuleDetail.vue");
//
var router = new VueRouter({
	routes: [{
		path: "/page1",
		component: firstCp,
		
	}, {
		path: "/page2",
		component: secondCp,
	}, {
		path: "/page3",
		component: threeCp,
	}, {
		path: "/page4",
		component: fourCp,
	}, {
		path: "/details",
		component: detailsCp,
	}, {
		path: "/homeDetail",
		component: homeDetailCp,
	}, {
		path: "/junshiDetail",
		component: junshiDetailCp,
	}, {
		path: "/yuleDetail",
		component: yuleDetailCp,
	}, {
		path: "/",
		redirect: '/page1',
	}]
});
//
Vue.component('headerCp', {
	template: `
    <div>
		<mu-appbar>
            <mu-icon-button icon="menu" slot="left" @click="toggle()" />
            <mu-flat-button href="#/page1" label="" slot="right" v-bind:class="{'isShow':getShow==1}" @click="dianji(1)"/>
            <mu-flat-button href="#/page2" label="" slot="right" v-bind:class="{'isShow':getShow==2}" @click="dianji(2)"/>
            <mu-flat-button href="#/page3" label="" slot="right" v-bind:class="{'isShow':getShow==3}" @click="dianji(3)"/>
            <mu-flat-button href="#/page4" label="" slot="right" v-bind:class="{'isShow':getShow==4}" @click="dianji(4)"/>
            <mu-icon-button icon="expand_more" slot="right" @click="loding()"/>  
            
	        <mu-paper class="demo-menu" v-bind:class="{'denglu':xianshi}">
	        <mu-menu>
	            <mu-menu-item title=""/>
	            <mu-menu-item title=""/>
	            <mu-menu-item title=""/>
	        </mu-menu>
	        </mu-paper>
        
        </mu-appbar>       
        
        <div :class="{'cebian':bool}">
        <mu-drawer :open="open" :docked="docked" @close="toggle()">
        <mu-list @itemClick="docked ? '' : toggle()">
           <mu-list-item title=":"/>
           <mu-list-item title=":"/>
           <mu-list-item title=":"/>
           <mu-list-item title=":"/>
           <mu-list-item title=":"/>
           <mu-list-item title=":"/>         
           <mu-list-item title=":"/>
           <mu-list-item v-if="docked" @click.native="open = false" title="Close"/>
        </mu-list>
        </mu-drawer>
        </div>
        
	</div>
	
	  `,
	data: function() { //
		return {
			bool:true,
			getShow: 1,
			open: false,
            docked: true,
            xianshi:false,
		}
	},
	methods: {
		dianji(num) {
			this.getShow = num
		},
		toggle (flag) {
            this.open = !this.open
            this.docked = !flag
      }, 
      loding(){
      	 this.xianshi=!this.xianshi
      }
		
	},

});
var store = new Vuex.Store({
	//
	state: {
		author: "xilong",
		NewsId: '',
		MeinvId: '',
		JunshiId: '',
		YuleId: '',
	},
	mutations: {
		//
		changeNewsId: function(state, a) {
			state.NewsId = a
		},
		changeMeinvId: function(state, a) {
			state.MeinvId = a
		},
		changeJunshiId: function(state, a) {
			state.JunshiId = a
		},
		changeYuleId: function(state, a) {
			state.YuleId = a
		}
	},
	//
	getters: {
		getNewsId: function(state) {
			return state.NewsId
		},
		getMeinvId: function(state) {
			return state.MeinvId
		}
	}
});
new Vue({
	el: "#demo",
	data: {
		name: 'long',
	},
	template: `
	    <div>
	        <headerCp></headerCp>
			<router-view></router-view>
		</div>
	`,
	//component: footercp,
	router,
	store,

})


